.button {
  height: 40px;
  width: 180px;
  margin: auto;          
  margin-top: 15px;
  color: var(--title-color);
  background: linear-gradient(45deg, var(--ligte-button-color) 30%, var(--button-color) 90%);
  border: 0;
  border-radius: 2%;
  box-shadow: 0 3px 5px 2px rgba(252, 118, 200, 0.3);
}

.button:active {
    transform: scale(0.8);
}

a {
  color: var(--title-color);
}

@media screen and (min-width: 768px) {
  .button {
    height: 45px;
    width: 200px;
    font-size: 18px; 
  }
}

@media screen and (min-width: 1280px) {
  .button {
    height: 60px;
    width: 240px;
    font-size: 22px; 
    margin-top: 15px;
  }
}